* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

h2 {
  font-size: 35px;
  margin: 0;
  margin-left: 10px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding-left: 5px;
}

.country-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.text {
  margin-left: 15px;
  margin-top: 0;
  margin-bottom: 0;
}

.title {
  display: flex;
  align-items: center;
}
